.clsParentDiv {
    margin-top: 5vh;
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    width: 350px;
    justify-content: center;
}

.clsTopDiv {
    margin-left: auto;
    margin-right: auto;
}

.clsTopDiv button {
    padding: 15px;
    margin: 1px;
    background-color: #0d6efd;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 3px;
}

.clsTopDiv button:hover {
    padding: 15px;
    margin: 1px;
    background-color: #79aefe;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 3px;
    cursor: pointer;
}

.clsDivCityName {
    background-color: azure;
    border: 1px solid lightgrey;
    padding: 22px;
    font-size: 1.2rem;
    text-align: center;
    display: flex;

}

.clsDivCityName img {
    height: 25px;
}

.clsDivCurrentTemp {
    border: 1px solid lightgrey;
    padding: 22px;
    font-size: 1.2rem;
}

.clsDivWindSpeed {
    border: 1px solid lightgrey;
    padding: 22px;
    font-size: 1.2rem;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}